<template>
  <div class="l-budget-creation">
    <v-layout row wrap>
      <span class="md-budget-state-hint uppercased white--text">status</span>
      <v-flex xs12 md2>
        <v-select
          label=""
          :items="states"
          v-model="budget.state"
        >
        </v-select>
      </v-flex>

      <v-flex xs12 md9 offset-md1>
        <v-select
          label=""
          :items="clients"
          v-model="budget.client"
          item-text="name"
          item-value="_id"
        >
        </v-select>
      </v-flex>

      <v-flex xs12 md12>
        <v-text-field label=""
                      v-model="budget.title"
                      required
                      color="light-blue lighten-1">
        </v-text-field>

        <v-text-field label=""
                      v-model="budget.description"
                      textarea
                      required
                      color="light-blue lighten-1">
        </v-text-field>

        <v-text-field label=" "
                      v-model="budget.profit"
                      box dark
                      prefix="%"
                      required
                      color="light-blue lighten-1">
        </v-text-field>
      </v-flex>

      <v-layout row wrap v-for="item in budget.items" class="l-budget-item" :key="item.id">
        <v-flex xs12 md1>
          <v-btn block dark color="red lighten-1" @click.native="removeItem(item)"></v-btn>
        </v-flex>

        <v-flex xs12 md3 offset-md1>
          <v-text-field label=""
                        box dark
                        v-model="item.title"
                        required
                        color="light-blue lighten-1">
          </v-text-field>
        </v-flex>

        <v-flex xs12 md1 offset-md1>
          <v-text-field label=""
                        box dark
                        prefix="$"
                        v-model="item.price"
                        required
                        color="light-blue lighten-1">
          </v-text-field>
        </v-flex>

        <v-flex xs12 md1 offset-md1>
          <v-text-field label=""
                        box dark
                        min="0"
                        v-model="item.quantity"
                        type="number"
                        required
                        color="light-blue lighten-1">
          </v-text-field>
        </v-flex>

        <v-flex xs12 md2>
          <span class="md-budget-item-subtotal white--text"> {{ item.subtotal }}</span>
        </v-flex>
      </v-layout>

      <v-flex xs12 md2 offset-md10>
        <v-btn block color="md-add-item-btn light-blue lighten-1" @click.native="addItem()"></v-btn>
      </v-flex>

      <v-layout row wrap v-for="item in budget.calcs" class="l-budget-item" :key="item.id">
        <v-flex xs12 md1>
          <v-btn block dark color="red lighten-1" @click.native="removeCalc(item)"></v-btn>
        </v-flex>

        <v-flex xs12 md3 offset-md1>
          <v-select
            label=""
            :items="budgets"
            v-model="item._id"
            item-text="title"
            item-value="_id"
          >
          </v-select>
        </v-flex>

        <v-flex xs12 md2>
          <span class="md-budget-item-subtotal white--text">c  {{ item.budget_item(item._id, budgets, 'total_price') }}</span>
        </v-flex>

        <v-flex xs12 md2>
          <span class="md-budget-item-subtotal white--text"> {{ item.budget_item(item._id, budgets, 'profit') }}</span>
        </v-flex>

        <v-flex xs12 md2>
          <span class="md-budget-item-subtotal white--text">  {{ (+item.budget_item(item._id, budgets, 'total_price') / 100.0) * (100.0 + +item.budget_item(item._id, budgets, 'profit')) }}</span>
        </v-flex>

      </v-layout>

      <v-flex xs12 md2 offset-md10>
        <v-btn block color="md-add-item-btn light-blue lighten-1" @click.native="addCalc()"></v-btn>
      </v-flex>

      <v-flex xs12 md2 offset-md10>
        <span class="md-budget-item-total white--text"> {{ budget.total_price }}</span>
      </v-flex>

      <v-flex xs12 md2 offset-md10>
        <span class="md-budget-item-total white--text">  {{ budget.total_revenue }}</span>
      </v-flex>

      <v-flex xs12 md2 offset-md10>
        <v-btn block color="md-add-item-btn green lighten-1" @click.native="saveBudget(budget)"></v-btn>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
  export default {
    props: ['clients', 'budgets', 'saveBudget'],
    data: function () {
      var globalBudgets = this.budgets
      return {
        budget: {
          title: null,
          description: null,
          profit: 0,
          state: '',
          client: null,
          get total_price () {
            let value = 0
            this.items.forEach(({ subtotal }) => {
              value += parseInt(subtotal)
            })
            this.calcs.forEach((calc) => {
              value += (+calc.budget_item(calc._id, globalBudgets, 'total_price') / 100.0) * (100.0 + +calc.budget_item(calc._id, globalBudgets, 'profit'))
            })
            return value.toFixed(6)
          },
          get total_revenue () {
            return ((this.total_price / 100.0) * (100.0 + +Math.abs(this.profit))).toFixed(6)
          },
          items: [
            {
              title: null,
              quantity: 0,
              price: 0,
              budgets: null,
              get subtotal () {
                return this.quantity * this.price
              }
            }
          ],
          calcs: [
            {
              _id: '',
              budget_item: function (id, budgets, key) {
                console.log(budgets)
                var result = budgets.filter(function (obj) {
                  return obj._id === id
                })
                var prop = result[0] ? result[0][key] : 0
                console.log('result[0] ', prop)
                return prop
              }
            }
          ]
        },
        states: [
          '', '', '', '', '', ''
        ]
      }
    },
    methods: {
      addItem () {
        const items = this.budget.items
        const item = {
          title: '',
          budgets: null,
          quantity: 0,
          price: 0,
          get subtotal () {
            return this.quantity * this.price
          }
        }

        items.push(item)
      },

      removeItem (selected) {
        const items = this.budget.items
        items.forEach((item, index) => {
          if (item === selected) {
            items.splice(index, 1)
          }
        })
      },

      addCalc () {
        const calcs = this.budget.calcs
        const calc = {
          _id: '',
          budget_item: function (id, budgets, key) {
            console.log(budgets)
            var result = budgets.filter(function (obj) {
              return obj._id === id
            })
            var prop = result[0] ? result[0][key] : 0
            console.log('result[0] ', prop)
            return prop
          }
        }

        calcs.push(calc)
      },

      removeCalc (selected) {
        const calcs = this.budget.calcs
        calcs.forEach((item, index) => {
          if (item === selected) {
            calcs.splice(index, 1)
          }
        })
      }
    }
  }
</script>

<style lang="scss">
  @import "./../../assets/styles";

  .uppercased {
    text-transform: uppercase;
  }

  .l-budget-creation {
    label, input, .icon, .input-group__selections__comma, textarea {
      color: #29b6f6!important;
    }

    .input-group__details {
      &:before {
        background-color: $border-color-input !important;
      }
    }

    .input-group__input {
      border-color: $border-color-input !important;

      .input-group--text-field__prefix {
        margin-bottom: 3px !important;
      }
    }

    .input-group--focused {
      .input-group__input {
        border-color: #29b6f6!important;
      }
    }
  }

  .md-budget-state-hint {
    margin: 10px 0;
    display: block;
    width: 100%;
  }

  .md-budget-state {
    background-color: rgba(41, 182, 246, .6);
    display: flex;
    height: 35px;
    width: 100%;
    font-size: 14px;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
    margin: 10px 0 15px;
  }

  .l-budget-item {
    align-items: center;
  }

  .md-budget-item-subtotal {
    font-size: 16px;
    text-align: center;
    display: block;
  }

  .md-budget-item-total {
    font-size: 22px;
    text-align: center;
    display: block;
    width: 100%;
    margin: 30px 0 10px;
  }

  .md-add-item-btn {
    margin-top: 30px !important;
    display: block;
  }

  .list__tile__title, .input-group__selections {
    text-transform: uppercase !important;
  }
</style>



